<template>
  <v-container>
    <v-dialog>
      <template v-slot:activator="{ on, attrs }">
        <v-card class="mx-auto" color="rgba(72, 52, 62, 0.12)">
          <v-toolbar
            color="#fcb69f"
            src="https://cdn.pixabay.com/photo/2019/05/14/21/50/storytelling-4203628_1280.jpg"
          >
            <template v-slot:img="{ props }">
              <v-img
                v-bind="props"
                gradient="to top right, rgba(146, 135, 22, 0.5), rgba(229, 209, 166, 0.8)"
              ></v-img>
            </template>
            <v-btn class="mx-2" fab icon fixed to="/home">
              <v-icon> mdi-arrow-left-bold </v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-toolbar-title
              ><h1 class="text-h4 font-weight-thin">
                Buscar Historias
              </h1></v-toolbar-title
            >
            <v-spacer></v-spacer>
            <v-btn icon @click="$refs.search.focus()">
              <v-icon>mdi-magnify</v-icon>
            </v-btn>
          </v-toolbar>
          <v-container class="py-0">
            <v-row align="center" justify="start">
              <v-col v-if="!allSelected" cols="12">
                <v-text-field
                  ref="search"
                  v-model="search"
                  full-width
                  background-color="rgba(72, 52, 62, 0.2)"
                  outline
                  color="black"
                  hide-details
                  label="Inserte el nombre de la historia"
                  single-line
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>

          <v-divider v-if="!allSelected"></v-divider>
          <template v-for="item in categories">
            <v-card
              color="rgba(0, 0, 0, 0.5)"
              v-if="!selected.includes(item)"
              :key="item.text"
              :disabled="loading"
              v-bind="attrs"
              v-on="on"
              @click="toPDF(item.historia, item.text, item.imagen)"
            >
              <v-img
                :src="item.imagen"
                aspect-ratio="1.7"
                class="white--text align-end"
                gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                height="300px"
              ></v-img>
              <v-card-title>{{ item.text }}</v-card-title>
            </v-card>
          </template>

          <v-divider></v-divider>
        </v-card>
      </template>
      <v-card class="overflow-hidden">
        <v-app-bar
          absolute
          color="dark"
          :src="Image"
          elevate-on-scroll
          scroll-target="#scrolling-techniques-7"
        >
          <v-btn color="rgba(0,0,0,.5)" @click="prevPage()">
            <v-icon left> mdi-arrow-left-thick </v-icon>Atrs</v-btn
          >
          <v-toolbar-title class="flex text-center">
            <h4>{{ Titulo }}</h4>
          </v-toolbar-title>
          <v-btn color="rgba(0,0,0,.5)" right absolute @click="nextPage()">
            Siguiente <v-icon right> mdi-arrow-right-thick </v-icon></v-btn
          >
        </v-app-bar>
        <v-sheet
          id="scrolling-techniques-7"
          class="overflow-y-auto"
          max-height="600"
        >
          <v-container style="height: 1500px">
            <div>
              <pdf
                :src="src"
                :page="currentPage"
                @page-loaded="currentPage = $event"
              ></pdf>
            </div>
          </v-container>
        </v-sheet>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { db } from "../main";
import pdf from "vue-pdf";
export default {
  components: {
    pdf,
  },
  data: () => ({
    items: [],
    src: null,
    Titulo: "",
    Image: "",
    numPages: undefined,
    currentPage: 1,
    loading: false,
    search: "",
    selected: [],
  }),

  computed: {
    allSelected() {
      return this.selected.length === this.items.length;
    },
    categories() {
      const search = this.search.toLowerCase();

      if (!search) return this.items;

      return this.items.filter((item) => {
        const text = item.text.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
    selections() {
      const selections = [];

      for (const selection of this.selected) {
        selections.push(selection);
      }

      return selections;
    },
  },

  watch: {
    selected() {
      this.search = "";
    },
  },

  methods: {
    next() {
      this.loading = true;

      setTimeout(() => {
        this.search = "";
        this.selected = [];
        this.loading = false;
      }, 2000);
    },
    HistoriasList() {
      db.collection("Historias").onSnapshot((querySnapshot) => {
        this.items.length = 0;
        querySnapshot.forEach((doc) => {
          this.items.push({
            text: doc.data().titulo,
            imagen: doc.data().imagen,
            historia: doc.data().historia,
            valoracion: doc.data().valoracion,
          });
        });
      });
    },
    toPDF(url, titulo, imag) {
      this.Titulo = titulo;
      this.Image = imag;
      this.currentPage = 1;
      this.src = null;
      this.src = pdf.createLoadingTask(url);
      this.src.promise.then((pdf) => {
        this.numPages = pdf.numPages;
      });
    },
    prevPage() {
      this.currentPage--;
    },
    nextPage() {
      this.currentPage++;
    },
  },
  mounted() {
    this.HistoriasList();
  },
};
</script>