<template>
  <div @click="overlay = false">
    <header>
      <v-app-bar v-if="!overlay" src="https://i.imgur.com/OCJhmiR.png">
        <v-toolbar src="https://i.imgur.com/OCJhmiR.png" flat shaped>
          <v-btn @click.stop="drawer = !drawer" text>
            <v-icon text>mdi-reorder-horizontal</v-icon>
            Menu
          </v-btn>
          <v-spacer></v-spacer>
            <v-toolbar-title class="text-h3 font-weight-thin mb-4"
              >HISTORIAS DEL MAGDALENA</v-toolbar-title
            >
          <v-spacer></v-spacer>
          <div align="right">
            <v-btn to="/login" :disabled="usuario" text>
              <v-icon text>mdi-account-cowboy-hat</v-icon>
              Ingresar
            </v-btn>
          </div>
        </v-toolbar>
      </v-app-bar>
    </header>
    <v-responsive>
      <Body/>
    </v-responsive>
    <!--Side-->
    <aside>
      <v-navigation-drawer
        dark
        src="https://64.media.tumblr.com/bd9a99c95d04cb7a1ae168f04b44a18c/2546c22727df9538-9f/s1280x1920/717cf285883422857098b3aed1dc261b8a2bd6ea.jpg"
        v-model="drawer"
        bottom
        temporary
        fixed
      >
        <v-list nav dense>
          <v-list-item
            v-for="(item, index) in items"
            :key="index"
            :to="item.hyper"
            link
            @click="ExitBtn(item.text, item.icon)"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </aside>
    <!--Overlay-->
    <v-overlay :z-index="zIndex" :value="overlay" :opacity="opacity">
      <v-img
        src="https://i.imgur.com/xJmgFlP.jpg"
        max-width="600"
        max-height="750"
      ></v-img>
    </v-overlay>
  </div>
</template>

<script>
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import Body from "./Body.vue";
import { db } from "../main";
import router from '../router';
export default {
  components: { Body },
  name: "HelloWorld",
  data: () => ({
    show: false,
    drawer: false,
    usuario: false,
    group: null,
    absolute: true,
    opacity: 0.85,
    overlay: true,
    zIndex: 0,
    items: [
      { icon: "mdi-incognito", text: "Anonimo" },
      { icon: "mdi-book", text: "Ttulos de Terror Csmico", hyper: "/about" },
      { icon: "mdi-web", text: "Subir Historia", hyper: "/upload" },
      { icon: "mdi-magnify", text: "Buscar Historias", hyper: "/search" },
      { icon: "mdi-exit-run", text: "Salir" },
    ],
  }),
  methods: {
    Exit() {
      firebase
        .auth()
        .signOut()
        .then(() => {})
        .catch((error) => {
          console.log(error);
        });
    },
    ExitBtn(text, icon) {
      if (text === "Salir") {
        this.Exit();
      }
      if (icon === "mdi-incognito" || icon === "mdi-account-check") {
        router.push("ProfileUser")
      }
    },
  },
  watch: {
    group() {
      this.drawer = false;
    },
  },
  mounted() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.items[0].text = user.displayName;
        this.usuario=true,
        this.items[0].icon = "mdi-account-check"; 
        db.collection("users").doc(user.uid).set({
          name: user.displayName,
          email: user.email,
        });
      } else {
        this.usuario=false,
        this.items[0].text = "Anonimo";
        this.items[0].icon = "mdi-incognito";
      }
    });
  },
};
</script>

<style>
#app {
  background: url("https://i.imgur.com/4byjHlq.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
</style>