<template>
  <div>
    <header>
      <v-btn class="mx-2" fab dark large plain fixed to="/home">
        <v-icon dark> mdi-arrow-left-bold-outline </v-icon>
      </v-btn>
    </header>
    <section>
      <v-row class="action" align="center" justify="center" absolute fixed>
        <v-col cols="12" sm="6" md="6">
          <v-card color="rgb(40, 0, 0, 0.2)">
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-col>
                <v-text-field
                  v-model="email"
                  :rules="emailRules"
                  label="Email"
                  required
                ></v-text-field>

                <v-text-field
                  v-model="password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[rules.required, rules.min]"
                  :type="show1 ? 'text' : 'password'"
                  name="input-10-1"
                  label="Contrasea"
                  hint="Minimo 8 caracteres"
                  counter
                  @click:append="show1 = !show1"
                  required
                ></v-text-field>
              </v-col>
              <v-btn text block dark @click="sheet = !sheet">
                Soy subnormal. He olvidado la contrasea
              </v-btn>
              <v-checkbox
                v-model="checkbox"
                :rules="[(v) => !!v || 'Acepta esto para continuar']"
                label="Aceptar que mi privacidad no es importante"
                required
              ></v-checkbox>

              <v-btn
                :disabled="!valid"
                color="rgb(0, 60, 0, 0.6)"
                class="mr-4"
                @click="submitUser()"
              >
                Ingresar datos
              </v-btn>
              <v-btn color="rgb(100, 0, 0, 0.6)" class="mr-4" @click="reset">
                Eliminar datos
              </v-btn>
              <v-btn fixed text icon color="red lighten-2" @click="submitGmail">
                <v-icon>mdi-gmail</v-icon>
              </v-btn>
            </v-form>
          </v-card>
        </v-col>
        <v-dialog v-model="dialog" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              block
              fab
              tile
              color="rgb(40, 0, 0, 0.2)"
              v-bind="attrs"
              v-on="on"
            >
              REGISTRARSE
            </v-btn>
          </template>
          <v-card>
            <v-col class="text-right">
              <v-btn icon dark @click="dialog = false">
                <v-icon>mdi-close-box</v-icon>
              </v-btn>
            </v-col>
            <v-card-title>
              <span class="text-h5">Creacin de Usuario</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6">
                    <v-text-field
                      v-model="form.name"
                      label="Nombre de Usuario"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      label="Email*"
                      v-model="form.email"
                      :rules="emailRules"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="form.password"
                      :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min]"
                      label="Contrasea*"
                      :type="show2 ? 'text' : 'password'"
                      @click:append="show2 = !show2"
                      hint="Minimo 8 caracteres"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
              <small>* representa casillas obligatorias </small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="ClearUserCr()">
                Eliminar datos
              </v-btn>
              <v-btn color="blue darken-1" text @click="submit()">
                Registrar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-bottom-sheet v-model="sheet">
          <v-sheet class="text-center" height="200px">
            <v-row no-gutters>
              <v-col md="6" offset-md="3">
                <v-card class="pa-2" tile flat>
                  <h1 class="text-h4 font-weight-thin mb-4">
                    Cambiar Contrasea
                  </h1>
                  <v-text-field
                    v-model="Changepassword"
                    label="Email"
                    hint="Recibir las instrucciones en su bandeja"
                  ></v-text-field>
                  <v-btn class="mt-6" text color="blue" @click="ChangePass()">
                    Ingresar
                  </v-btn>
                  <v-btn class="mt-6" text color="grey" @click="sheet = !sheet">
                    Cerrar
                  </v-btn>
                </v-card>
              </v-col>
            </v-row>
          </v-sheet>
        </v-bottom-sheet>
      </v-row>
    </section>
  </div>
</template>

<script>
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import "firebase/app";
import "firebase/firestore/lite";
import router from '../router';

export default {
  data: () => ({
    show1: false,
    show2: false,
    dialog: false,
    sheet: false,
    Changepassword: "",
    rules: {
      required: (value) => !!value || "Esto es requerido",
      min: (v) => v.length >= 8 || "Minimo 8 caracteres",
    },
    valid: true,
    name: "",
    password: "",
    email: "",
    emailRules: [
      (v) => !!v || "Se requiere Email",
      (v) => /.+@.+\..+/.test(v) || "El Email no es valido",
    ],
    select: null,
    checkbox: false,
    form: {
      name: "",
      email: "",
      password: "",
    },
    error: null,
  }),

  methods: {
    submit() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.form.email, this.form.password)
        .then((data) => {
          data.user
            .updateProfile({
              displayName: this.form.name,
            })
            .then(() => {
              firebase
                .auth()
                .currentUser.sendEmailVerification()
                .then(() => {
                  router.push("Home");
                });
              firebase
                .auth()
                .signOut()
                .then(() => {});
            });
        })
        .catch((err) => {
          this.error = err.message;
        });
    },
    submitUser() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          router.push("Home");
        })
        .catch((error) => {
          console.log(error);
        });
    },
    submitGmail() {
      var provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(() => {
          router.push("Home");
        })
        .catch((error) => {
          console.log(error);
        });
    },
    ChangePass() {
      firebase
        .auth()
        .sendPasswordResetEmail(this.Changepassword)
        .then(() => {
          this.Changepassword = "";
        })
        .catch((error) => {
          var errorCode = error.code;
          var errorMessage = error.message;
          console.log(errorCode);
          console.log(errorMessage);
        });
    },
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    ClearUserCr() {
      this.form.password = "";
      this.form.name = "";
      this.form.email = "";
    },
  },
  mounted() {},
  created() {},
};
</script>