<template>
  <div>
    <div>
      <v-btn class="mx-2" fab dark large plain fixed to="/home">
        <v-icon dark> mdi-arrow-left-bold-outline </v-icon>
      </v-btn>
    </div>
    <v-row align="center" justify="center" absolute fixed>
      <v-col sm="5">
          <v-card color="rgb(0, 0, 0, 0.7)">
            <v-col cols="12">
              <h1
                class="text-h4 font-weight-thin mb-4"
                style="background: rgba(0, 0, 0, 0.8)"
              >
                Subir una historia
              </h1>
              <v-text-field
                label="Titulo"
                v-model="Titulo"
                color="white"
                v-on:input="Condiciones()"
              ></v-text-field>
              <v-textarea
                color="white"
                v-model="Sinopsis"
                v-on:input="Condiciones()"
              >
                <template v-slot:label>
                  <div>Sinopsis</div>
                </template>
              </v-textarea>
              <v-file-input
                label="Portada"
                v-model="fileImage"
                accept="image/*"
                filled
                prepend-icon="mdi-camera"
                v-on:input="Condiciones()"
              ></v-file-input>
              <v-file-input
                accept=".pdf"
                v-model="filePDF"
                show-size
                label="Historia"
                v-on:input="Condiciones()"
              ></v-file-input>
              <v-row align="center" justify="center" absolute fixed>
                <v-btn
                  class="ma-2 white--text"
                  :disabled="!BotonDeSubida"
                  @click="uploadStory()"
                  outlined
                >
                  Subir historia
                  <v-icon right dark> mdi-cloud-upload </v-icon>
                </v-btn>
              </v-row>
            </v-col>
          </v-card>
      </v-col>
    </v-row>
    <v-snackbar v-model="Subida">
      {{ textsubida }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="Subida = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import firebase from "firebase/compat/app";
import { db } from "../main";
export default {
  data: () => ({
    Subida: false,
    textsubida: "",
    fileImage: null,
    urlImage: "",
    BotonDeSubida: false,
    pathImage: null,
    pathPDF: null,
    filePDF: null,
    urlPDF: "",
    Titulo: "",
    Sinopsis: "",
  }),
  methods: {
    Condiciones() {
      if (
        this.Titulo === "" ||
        this.Sinopsis === ""
      ) {
        this.BotonDeSubida = false;
      } else {
        this.BotonDeSubida = true;
      }
    },
    async uploadStory() {
      if (this.fileImage != null && this.filePDF != null) {
        const user = firebase.auth().currentUser;
        await this.fileInputIMG();
        await this.fileInputPDF();
        db.collection("Historias")
          .doc(user.uid + this.Titulo)
          .set({
            titulo: this.Titulo,
            autorID: user.uid,
            libroID: user.uid + this.Titulo,
            autor: user.displayName,
            sinopsis: this.Sinopsis,
            imagen: this.urlImage,
            historia: this.urlPDF,
            valoracion: 0,
          });
        this.Subida = true;
        this.textsubida = "La historia ha sido cargada con xito";
      } else {
        this.Subida = true;
        this.textsubida = "Error al subir la historia";
      }
      (this.fileImage = null),
        (this.filePDF = null),
        (this.Sinopsis = ""),
        (this.Titulo = "");
    },
    async fileInputIMG() {
      const storageRef = firebase.storage().ref();
      var path = "Imagenes/portada_" + this.fileImage.name;
      var urlI;
      await storageRef.child(path).put(this.fileImage);
      this.pathImage = path;
      await storageRef
        .child(this.pathImage)
        .getDownloadURL()
        .then((url) => {
          urlI = url;
        });
      this.urlImage = urlI;
    },
    async fileInputPDF() {
      const storageRef = firebase.storage().ref();
      var path = "Libros/story_" + this.filePDF.name;
      var urlI;
      await storageRef.child(path).put(this.filePDF);
      this.pathPDF = path;
      await storageRef
        .child(this.pathPDF)
        .getDownloadURL()
        .then((url) => {
          urlI = url;
        });
      this.urlPDF = urlI;
    },
  },
  mounted() {
    this.Condiciones();
  },
};
</script>

<style>
h1 {
  width: 500px;
  margin: 0 auto;
  text-align: center;
}
</style>