<template>
  <v-container>
    <v-row>
      <div>
        <v-banner
          color="rgba(176, 127, 0, 0.8)"
          dark
          elevation="24"
          shaped
          width="800"
          >Mis Historias</v-banner
        >
      </div>
      <v-dialog>
        <template v-slot:activator="{ on, attrs }">
          <v-container fluid>
            <v-row dense>
              <v-col
                v-for="story in Escritos"
                :key="story.title"
                cols="12"
                sm="4"
              >
                <v-card color="rgba(0, 0, 0, 0.5)">
                  <v-img
                    :src="story.imagen"
                    aspect-ratio="1.7"
                    class="white--text align-end"
                    gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                    height="300px"
                  ></v-img>

                  <v-card-title>
                    {{ story.title }}
                  </v-card-title>

                  <v-card-subtitle>
                    {{ story.sinops | truncate(200) }}
                  </v-card-subtitle>
                  <v-card-actions>
                    <v-btn
                      outlined
                      rounded
                      text
                      color="green lighten-4"
                      v-bind="attrs"
                      v-on="on"
                      @click="toPDF(story.historia, story.title, story.imagen)"
                    >
                      Leer
                    </v-btn>
                    <v-btn
                      class="ma-1"
                      color="error"
                      plain
                      @click="Eliminar(story.historia, story.id, story.imagen)"
                    >
                      Eliminar
                    </v-btn>
                    <v-spacer> </v-spacer>
                    <v-rating
                      v-model="story.valoracion"
                      readonly
                      background-color="white"
                      color="yellow accent-4"
                      dense
                      half-increments
                      hover
                      size="18"
                    >
                    </v-rating>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </template>
        <v-card class="overflow-hidden">
          <v-app-bar
            absolute
            color="dark"
            :src="Image"
            elevate-on-scroll
            scroll-target="#scrolling-techniques-7"
          >
            <v-btn color="rgba(0,0,0,.5)" @click="prevPage()">
              <v-icon left> mdi-arrow-left-thick </v-icon>Atrs</v-btn
            >
            <v-toolbar-title class="flex text-center">
              <h4>{{ Titulo }}</h4>
            </v-toolbar-title>
            <v-btn color="rgba(0,0,0,.5)" right absolute @click="nextPage()">
              Siguiente <v-icon right> mdi-arrow-right-thick </v-icon></v-btn
            >
          </v-app-bar>
          <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            max-height="600"
          >
            <v-container style="height: 1500px">
              <div>
                <pdf
                  :src="src"
                  :page="currentPage"
                  @page-loaded="currentPage = $event"
                ></pdf>
              </div>
            </v-container>
          </v-sheet>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>
<script>
import firebase from "firebase/compat/app";
import pdf from "vue-pdf";
import { db } from "../main";
export default {
  components: {
    pdf,
  },
  data: () => ({
    show: false,
    src: null,
    Titulo: "",
    Image: "",
    numPages: undefined,
    currentPage: 1,
    story: false,
    Escritos: [],
  }),
  methods: {
    HistoriasList() {
      const user = firebase.auth().currentUser;
      db.collection("Historias").onSnapshot((querySnapshot) => {
        this.Escritos.length = 0;
        querySnapshot.forEach((doc) => {
          if (user.uid === doc.data().autorID) {
            this.Escritos.push({
              title: doc.data().titulo,
              id: doc.data().libroID,
              sinops: doc.data().sinopsis,
              imagen: doc.data().imagen,
              historia: doc.data().historia,
              valoracion: doc.data().valoracion,
            });
          }
        });
      });
    },
    async Eliminar(Historia, Titulo, Imagen) {
      var storage = firebase.storage();
      var httpsReference = storage.refFromURL(Historia);
      var httpsImagen = storage.refFromURL(Imagen);
      await storage.ref().child(httpsReference.fullPath).delete();
      await storage.ref().child(httpsImagen.fullPath).delete();
      await db.collection("Historias").doc(Titulo).delete();
      this.HistoriasList();
    },
    toPDF(url, titulo, imag) {
      this.Titulo = titulo;
      this.Image = imag;
      this.currentPage = 1;
      this.src = null;
      this.src = pdf.createLoadingTask(url);
      this.src.promise.then((pdf) => {
        this.numPages = pdf.numPages;
      });
    },
    prevPage() {
      this.currentPage--;
    },
    nextPage() {
      this.currentPage++;
    },
  },
  mounted() {
    this.HistoriasList();
  },
};
</script>