<template>
  <div>
    <div>
      <v-btn class="mx-2" fab dark large plain fixed to="/home">
        <v-icon dark> mdi-arrow-left-bold-outline </v-icon>
      </v-btn>
    </div>
    <v-menu v-model="showMenu" absolute offset-y style="max-width: 600px">
      <template v-slot:activator="{ on, attrs }" fixed>
        <v-card class="mx-auto" max-width="1000" tile>
          <v-card tile v-bind="attrs" v-on="on">
            <v-img height="200" :src="ImagePortada"></v-img>
          </v-card>
          <v-row style="margin: 2.5%; position: absolute; top: 130px">
            <v-list-item>
              <v-list-item-avatar size="100">
                <v-img :src="ImagePerfil" alt="No image">
                  <v-btn
                    class="align-self-center some-extra-styles"
                    text
                    block
                    x-large
                    @click="profilesheet = !profilesheet"
                  ></v-btn>
                </v-img>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="title" style="margin-top: 20px">{{
                  nameUser
                }}</v-list-item-title>
                <v-list-item-subtitle>Escritor/a</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-row>
        </v-card>
      </template>
      <v-list>
        <v-btn text block plain tile depressed @click="sheet = !sheet">{{
          ChangeImageWallpaper
        }}</v-btn>
      </v-list>
    </v-menu>
    <v-responsive>
      <div class="pa-8"></div>
      <HistoriasSubidas />
    </v-responsive>
    <!---ADMIN-->
    <v-dialog v-model="dialog" width="600px" v-if="admin">
      <template v-slot:activator="{ on, attrs }">
        <v-item-group>
          <v-container>
            <div style="flex: 1; padding: 70px">
              <v-row justify="center">
                <v-col v-for="n in 3" :key="n" cols="12" md="4">
                  <v-item v-slot="{ active, toggle }">
                    <v-card
                      :color="active ? 'transparent' : 'rgb(127, 0, 255, 0.6)'"
                      class="d-flex align-center"
                      outlined
                      height="200"
                      @click="toggle"
                    >
                      <v-scroll-y-transition>
                        <div
                          v-if="active"
                          class="text-h2 flex-grow-1 text-center"
                          v-bind="attrs"
                          v-on="on"
                          @click="obtainAc"
                        >
                          Usuarios
                        </div>
                      </v-scroll-y-transition>
                    </v-card>
                  </v-item>
                </v-col>
              </v-row>
            </div>
          </v-container>
        </v-item-group>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">Lista de Usuarios</span>
        </v-card-title>
        <v-list>
          <template>
            <v-list-item v-for="item in people" :key="item.nombre">
              <v-list-item-content>
                <v-list-item-title v-text="item.nombre"> </v-list-item-title>
                <v-list-item-subtitle
                  v-text="item.correo"
                ></v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action>
                <v-btn icon>
                  <v-icon color="grey lighten-1">mdi-account-cancel</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>
          </template>
        </v-list>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-bottom-sheet v-model="sheet">
      <v-sheet class="text-center" height="200px">
        <v-row no-gutters>
          <v-col md="6" offset-md="3">
            <v-card class="pa-2" tile flat>
              <h1 class="text-h4 font-weight-thin mb-4">Cambiar Portada</h1>
              <v-file-input
                label="Imagen de portada"
                v-model="fileImage"
                placeholder="Selecciona una imagen"
                accept="image/*"
                prepend-icon="mdi-camera"
              ></v-file-input>
              <v-btn class="mt-6" text color="blue" @click="changeWallpaper()">
                Cambiar
              </v-btn>
              <v-btn class="mt-6" text color="grey" @click="sheet = !sheet">
                Cancelar
              </v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-sheet>
    </v-bottom-sheet>
    <v-bottom-sheet v-model="profilesheet">
      <v-sheet class="text-center" height="200px">
        <v-row no-gutters>
          <v-col md="6" offset-md="3">
            <v-card class="pa-2" tile flat>
              <h1 class="text-h4 font-weight-thin mb-4">Cambiar Perfil</h1>
              <v-file-input
                label="Imagen de perfil"
                v-model="fileImage"
                placeholder="Selecciona una imagen"
                accept="image/*"
                prepend-icon="mdi-camera"
              ></v-file-input>
              <v-btn class="mt-6" text color="blue" @click="changeProfile()">
                Cambiar
              </v-btn>
              <v-btn
                class="mt-6"
                text
                color="grey"
                @click="profilesheet = !profilesheet"
              >
                Cancelar
              </v-btn>
            </v-card>
          </v-col>
        </v-row>
      </v-sheet>
    </v-bottom-sheet>
  </div>
</template>
<script>
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import { db } from "../main";
import HistoriasSubidas from "./HistoriasSubidas.vue";
export default {
  components: { HistoriasSubidas },
  data: () => ({
    nameUser: "",
    image: "",
    dialog: false,
    people: [],
    model: 1,
    admin: false,
    sheet: false,
    profilesheet: false,
    showMenu: false,
    ChangeImageWallpaper: "Cambiar Imagen",
    fileImage: null,
    urlImage: "",
    urlPerfil: "",
    pathImage: null,
    ImagePortada: "",
    ImagePerfil: "",
  }),
  methods: {
    obtainAc() {
      this.people.length = 0;
      db.collection("users")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            let char = doc.data();
            this.people.push({
              id: doc.id,
              perfil: char.usuario,
              nombre: char.name,
              correo: char.email,
            });
          });
        })
        .catch((error) => {
          console.log("Error getting documents: ", error);
        });
    },
    async uploadWallpaper() {
      const storageRef = firebase.storage().ref();
      const user = firebase.auth().currentUser;
      var path = "Usuarios/" + user.uid;
      var urlI;
      await storageRef.child(path).put(this.fileImage);
      this.pathImage = path;
      await storageRef
        .child(this.pathImage)
        .getDownloadURL()
        .then((url) => {
          urlI = url;
        });
      this.urlImage = urlI;
      this.ImagePortada = urlI;
    },
    async changeWallpaper() {
      if (this.fileImage != null) {
        await this.uploadWallpaper();
        const user = firebase.auth().currentUser;
        db.collection("Portadas").doc(user.uid).set({
          urlP: this.urlImage,
        });
      }
      this.fileImage = null;
      this.sheet = false;
    },
    async uploadProfile() {
      const storageRef = firebase.storage().ref();
      const user = firebase.auth().currentUser;
      var path = "Perfiles/" + user.uid;
      var urlI;
      await storageRef.child(path).put(this.fileImage);
      this.pathImage = path;
      await storageRef
        .child(this.pathImage)
        .getDownloadURL()
        .then((url) => {
          urlI = url;
        });
      this.urlPerfil = urlI;
      this.ImagePerfil = urlI;
    },
    async changeProfile() {
      if (this.fileImage != null) {
        await this.uploadProfile();
        const user = firebase.auth().currentUser;
        db.collection("Perfiles").doc(user.uid).set({
          urlP: this.urlPerfil,
        });
      }
      this.fileImage = null;
      this.profilesheet = false;
    },
  },
  mounted() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.nameUser = user.displayName;
        this.image = user.photoURL;
        this.admin = false;
        var docRef = db.collection("admin").doc(user.uid);
        docRef
          .get()
          .then((doc) => {
            if (doc.exists) {
              this.admin = true;
            } else {
              this.admin = false;
            }
          })
          .catch((error) => {
            console.log("Error getting document:", error);
          });
        var Portada = db.collection("Portadas").doc(user.uid);
        Portada.get().then((doc) => {
          this.ImagePortada = doc.data().urlP;
        });
        var Profile = db.collection("Perfiles").doc(user.uid);
        Profile.get().then((doc) => {
          if (doc.exists) {
            this.ImagePerfil = doc.data().urlP;
          }
        });
      } else {
        this.nameUser = "John Doe";
        this.admin = false;
        this.image =
          "https://i.pinimg.com/736x/df/0c/09/df0c09967d74c081e14adcd8667fa2f9.jpg";
      }
    });
  },
};
</script>
<style>
.v-btn:before {
  opacity: 0 !important;
}
</style>