<template>
  <v-container>
    <v-row>
      <v-dialog>
        <template v-slot:activator="{ on, attrs }">
          <v-container fluid>
            <v-row dense>
              <v-col
                v-for="story in Stories"
                :key="story.title"
                cols="12"
                sm="4"
              >
                <v-card color="rgba(0, 0, 0, 0.5)">
                  <v-img
                    :src="story.imagen"
                    aspect-ratio="1.7"
                    class="white--text align-end"
                    gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                    height="300px"
                  ></v-img>

                  <v-card-title>
                    {{ story.title }}
                  </v-card-title>

                  <v-card-subtitle>
                    {{ story.sinops | truncate(200) }}
                  </v-card-subtitle>
                  <v-card-actions>
                    <v-btn
                      rounded
                      plain
                      large
                      color="light-green"
                      v-bind="attrs"
                      v-on="on"
                      @click="toPDF(story.historia, story.title, story.imagen)"
                    >
                      Leer
                    </v-btn>
                    <v-spacer> </v-spacer>
                    <v-rating
                      v-model="story.valoracion"
                      color="yellow darken-3"
                      background-color="grey darken-1"
                      empty-icon="$ratingFull"
                      half-increments
                      hover
                      dense
                      size="20"
                      @input="handleRatingChange($event, story.id)"
                    ></v-rating>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </v-container>
        </template>
        <v-card class="overflow-hidden">
          <v-app-bar
            absolute
            color="dark"
            :src="Image"
            elevate-on-scroll
            scroll-target="#scrolling-techniques-7"
          >
            <v-btn color="rgba(0,0,0,.5)" @click="prevPage()">
              <v-icon left> mdi-arrow-left-thick </v-icon>Atrs</v-btn
            >
            <v-toolbar-title class="flex text-center">
              <h4>{{ Titulo }}</h4>
            </v-toolbar-title>
            <v-btn color="rgba(0,0,0,.5)" right absolute @click="nextPage()">
              Siguiente <v-icon right> mdi-arrow-right-thick </v-icon></v-btn
            >
          </v-app-bar>
          <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            max-height="600"
          >
            <v-container style="height: 1500px">
              <div>
                <pdf
                  :src="src"
                  :page="currentPage"
                  @page-loaded="currentPage = $event"
                ></pdf>
              </div>
            </v-container>
          </v-sheet>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>
<script>
import firebase from "firebase/compat/app";
import pdf from "vue-pdf";
import { db } from "../main";
import router from "../router";

export default {
  components: {
    pdf,
  },
  data: () => ({
    show: false,
    src: null,
    Titulo: "",
    Image: "",
    numPages: undefined,
    currentPage: 1,
    story: false,
    Stories: [],
    puntos: 0,
    nVotos: 0,
  }),
  methods: {
    async handleRatingChange(props, titulo) {
      const user = firebase.auth().currentUser;
      if (user) {
        var Ref = db.collection("Historias");
        await Ref.doc(titulo).collection("Estrellas").doc(user.uid).set({
          valoracion: props,
        });
        await this.Valoracion(titulo);
        this.HistoriasList();
      } else {
        router.push("Login");
      }
    },
    async Valoracion(titulo) {
      this.nVotos = 0;
      this.puntos = 0;
      await db
        .collection("Historias")
        .doc(titulo)
        .collection("Estrellas")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.puntos = this.puntos + doc.data().valoracion;
            this.nVotos++;
          });
          db.collection("Historias")
            .doc(titulo)
            .update({ valoracion: this.puntos / this.nVotos });
        });
    },
    HistoriasList() {
      db.collection("Historias").onSnapshot((querySnapshot) => {
        this.Stories.length = 0;
        querySnapshot.forEach((doc) => {
          this.Stories.push({
            title: doc.data().titulo,
            id: doc.data().libroID,
            sinops: doc.data().sinopsis,
            imagen: doc.data().imagen,
            historia: doc.data().historia,
            valoracion: doc.data().valoracion,
          });
        });
      });
    },
    toPDF(url, titulo, imag) {
      var storage = firebase.storage();
      this.Titulo = titulo;
      this.Image = imag;
      var httpsRef=storage.refFromURL(url);
      this.currentPage = 1;
      this.src = null;
      this.src = pdf.createLoadingTask(url);
      this.src.promise.then((pdf) => {
        this.numPages = pdf.numPages;
      });
      console.log(httpsRef);
    },
    prevPage() {
      this.currentPage--;
    },
    nextPage() {
      this.currentPage++;
    },
  },
  mounted() {
    this.HistoriasList();
  },
};
</script>